import React, { Component } from 'react';
import Nav from "./components/Nav";
import Article from "./components/Article";
import NowLoading from "./components/NowLoading";

class App extends Component {
  state = {
    article: {
      item: { title: 'Welcome', desc: 'Hello, React & Ajax' },
      isLoading: false
    },
    list: {
      items: [],
      isLoading: false
    }
  }
  componentDidMount() {
    var newList = Object.assign({}, this.state.list, { isLoading: true });
    this.setState({ list: newList });
    fetch('react-ajax-app/list.json')
      .then(function (result) {
        return result.json();
      })
      .then(function (json) {
        console.log(json);
        this.setState({
          list: {
            items: json,
            isLoading: false
          }
        });
      }.bind(this));
  }
  render() {
    var NavTag = null;
    if (this.state.list.isLoading) {
      NavTag = <NowLoading></NowLoading>
    } else {
      NavTag = <Nav list={this.state.list.items} onClick={function (id) {
        var newArticle = Object.assign({}, this.state.article, { isLoading: true });
        this.setState({ article: newArticle });
        fetch('react-ajax-app/' + id + '.json')
          .then(function (result) {
            return result.json();
          })
          .then(function (json) {
            this.setState({
              article: {
                item: {
                  title: json.title,
                  desc: json.desc
                },
                isLoading: false
              }
            })
          }.bind(this));
      }.bind(this)}></Nav>
    }

    var ArticleTag = null;
    if (this.state.article.isLoading) {
      ArticleTag = <NowLoading></NowLoading>
    } else {
      ArticleTag = <Article title={this.state.article.item.title} desc={this.state.article.item.desc}></Article>
    }

    return (
      <div className="App">
        <h1>WEB</h1>
        {NavTag}
        {ArticleTag}
      </div>
    )
  }
}

export default App;
